/* ------------qnadetailview 부분------------- */

.board_wrap {
    width: 1000px;
    margin: 100px auto;
}

.board_title {
    margin-bottom: 30px;
}

.board_title strong {
    font-size: 2.5rem;
}

.board_title p {
    margin-top: 5px;
    font-size: 1.4rem;
}

.bt_wrap {
    margin-top: 30px;
    text-align: center;
    font-size: 0;
}

.bt_wrap button {
    float: right;
    margin: 0 10px 20px 10px;
    border: 1px solid #004175;
    min-width: 80px;
    margin-left: 10px;
    padding: 10px;
    font-size: 1.4rem;
    border-radius: 10px;
    font-weight: 500;
    background: #5baaf5;
    border: none;
    border-radius: 10px;
    color: white;
}

.bt_wrap button:first-child {
    margin-left: 0;
}

.bt_wrap button:hover {
    font-weight: bolder;
    background: #0079db;
    color: #fff;
    cursor: pointer;
}

/*---------qnalist-------------*/

.board_list {
    width: 100%;
    border-top: 2px solid #000;
}

.board_list > div {
    border-bottom: 1px solid #ddd;
}

.board_list > div.top {
    border-bottom: 1px solid #999;
}

.board_list > div:last-child {
    border-bottom: 1px solid #000;
}

.board_list > div > div {
    display: inline-block;
    padding: 15px 0;
    text-align: center;
    font-size: 1.25rem;
}

.board_list > div.top > div {
    font-weight: 600;
}

.board_list .num {
    width: 10%;
}

.board_list .title {
    width: 60%;
    text-align: center;
}

.board_list .top .title {
    text-align: center;
}

.board_list .writer {
    width: 12%;
}

.board_list .date {
    width: 18%;
}
