/*---------qnadetailview-------------*/

.board_view {
    width: 100%;
    border-top: 2px solid #000;
}

.board_view .title {
    padding: 20px 15px;
    border-bottom: 1px dashed #ddd;
    font-size: 1.6rem;
}

.board_view .info {
    padding: 15px;
    border-bottom: 1px solid #999;
    font-size: 0;
}

.board_view .info dl {
    position: relative;
    display: inline-block;
    padding: 0 20px;
}

.board_view .info dl:first-child {
    padding-left: 0;
}

.board_view .info dl::before {
    content: "";
    position: absolute;
    top: 1px;
    left: 0;
    display: block;
    width: 1px;
    height: 13px;
    background: #ddd;
}

.board_view .info dl:first-child::before {
    display: none;
}

.board_view .info dl dt,
.board_view .info dl dd {
    display: inline-block;
    font-size: 1.2rem;
}



.board_view .info dl dd {
    margin-left: 10px;
    color: #777;
}

.board_view .cont {
    padding: 15px;
    border-bottom: 1px solid #000;
    line-height: 160%;
    font-size: 1.4rem;
}


/*---------qnainsert-------------*/

.board_write {
    border-top: 2px solid #000;
}

.board_write .title,
.board_write .info {
    padding: 15px;
}

.board_write .info {
    border-top: 1px dashed #ddd;
    border-bottom: 1px solid #000;
    font-size: 0;
}

.board_write .title dl {
    font-size: 0;
}

.board_write .info dl {
    display: inline-block;
    width: 50%;
    vertical-align: middle;
    margin-left: 42%;
}

.board_write .title dt,
.board_write .title dd,
.board_write .info dt,
.board_write .info dd {
    display: inline-block;
    vertical-align: middle;
    font-size: 1.4rem;
}

.board_write .title dd {
    width: 80%;
}

.board_write .title input[type="text"],
.board_write .info input[type="text"] {
    padding: 10px;
    box-sizing: border-box;
}

.board_write .title input[type="text"] {
    width: 80%;
}

.board_write .cont {
    border-bottom: 1px solid #000;
}

.contcomment {
    padding: 2px;
    background-color: #eef7fc;
    border-bottom: 0.5px solid #000;
}

.contcomment p{
    font-weight: bold;
    font-size: large;
    color: rgb(49, 49, 49);
}
.contcomment .commentmain{
    float: left;
    display: block;
    width: 21%;
    padding:10px;
}
.contcomment .commentinfo{
    margin-left: 23%;
    width: 73%;
    padding:10px;
}

.board_write .cont textarea {
    display: block;
    width: 100%;
    height: 300px;
    padding: 15px;
    box-sizing: border-box;
    border: 0;
    resize: vertical;
}


.bt_wrap input {
    float: right;
    margin: 0 10px 20px 10px;
    border: 1px solid #004175;
    min-width: 80px;
    margin-left: 10px;
    padding: 10px;
    font-size: 1.4rem;
    border-radius: 10px;
    font-weight: 500;
    background: #5baaf5;
    border: none;
    border-radius: 10px;
    color: white;
}

.bt_wrap input:first-child {
    margin-left: 0;
    margin-right: 10px;
}

.bt_wrap input:hover {
    font-weight: bolder;
    background: #0079db;
    color: #fff;
    cursor: pointer;
}
.board_write .info .dnone{
    display: none;
}
.dnone{
    display: none;
}