.navbar {
    background: #fff;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
}

.navbar-container {
display: flex;
justify-content: space-between;
height: 80px;
}

.container {
z-index: 1;
width: 100%;
max-width: 1300px;
margin-right: auto;
margin-left: auto;
padding-right: 50px;
padding-left: 50px;
}

.navbar-logo {
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    display: flex;
    align-items: center;
}

.logoImgDiv {
    width: 270px;
    height: 75px;
}

.logoImg {
    max-width: 100%;
    max-height: 80%;
    margin-top: 13px;
}

.navbar-icon {
margin-right: 0.5rem;
}

.nav-menu {
display: flex;
align-items: center;
justify-content: center;
list-style: none;
text-align: center;
justify-content: end;
}

.nav-item {
height: 80px;
border-bottom: 2px solid transparent;
}

.nav-item:hover {
border-bottom: 2px solid #40A6FB;
}

.nav-links {
color: #000;
display: flex;
align-items: center;
text-decoration: none;
padding: 0.5rem 1rem;
height: 100%;
}

.fa-bars {
color: #000;
}

.menu-icon {
display: none;
}

@media screen and (max-width: 960px) {
.NavbarItems {
    position: relative;
}

.nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
}

.nav-menu.active {
    background: #fff;
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 1;
}

.nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
}

.nav-links:hover {
    color: #f00946;
    transform: scale(1.2);
    transition: all 0.3s ease;
}

.nav-item:hover {
    border: none;
}

.nav-item {
    width: 100%;
}

.navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
}

.menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
}

.fa-times {
    color: #000;
    font-size: 2rem;
}

.nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
}
}